.page {
  font-family: "Cinzel", serif;
  scroll-behavior: smooth;
  cursor: url(../images/Vector.svg), auto;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      z-index: 2;
    }
  }
}
